.controlPanel {
    position: absolute;
    width: 90%;
    left: 5%;
    top: 50px;
    height: 40px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    z-index: 9999;
    margin-bottom: 2%;

}


.lessonButton {
    border-radius: 5px;
    font-size: 16px;
    padding: 6px 12px;
    font-weight: 700;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#ffcc4d), to(#ffac33));
    background-image: -webkit-linear-gradient(#ffcc4d, #ffac33);
    background-image: -o-linear-gradient(#ffcc4d, #ffac33);
    background-image: linear-gradient(#ffcc4d, #ffac33);
    border-color: #f1a02a;
    color: #292f33 !important;
    text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
    height: auto;
    margin-left: 5px;
    margin-right: 5px;



}

.lessonButton:hover {
    background-color: #e99110;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#ffcc4d), to(#e99110));
    background-image: -webkit-linear-gradient(#ffcc4d, #e99110);
    background-image: -o-linear-gradient(#ffcc4d, #e99110);
    background-image: linear-gradient(#ffcc4d, #e99110);
    -ms-filter: 'progid:DXImageTransform.Microsoft.gradient(startColorstr=#ffcc4d,  endColorstr=#e99110,  GradientType=0)';
    border-color: #ec8b11;
    color: #292f33 !important;

}

.lessonButton:active {
  background-color: #f2a330;
  background-image: none;
  -webkit-box-shadow: inset 0 1px 4px rgba(0, 0, 0, 0.3);
          box-shadow: inset 0 1px 4px rgba(0, 0, 0, 0.3);

}



